/*  */
import './index.less';
import 'swiper/dist/css/swiper.min.css';
import React from 'react';
import { Row, Col, Tag, Typography, Divider } from 'antd';
import Swiper from 'swiper/dist/js/swiper.js';
import Conf from './conf/index.jsx';

const Mock = require('mockjs'),
{ Paragraph } = Typography;

class Swiperloop extends React.Component {
  swiperitem = (len) => {
    let list = [];
    for (let i=0; i<len; i++) {
      list.push(
        <div key={i} className="swiper-slide">
          <img className="picitem" src={Mock.Random.dataImage('500x300', 'Image_'+i)} alt="" />
        </div>
      );
    }
    return list;
  };
  handleSubmit = conf => {
    this.state.swiperTpl.destroy(true, true);
    this.setState({
      swiperConf: conf,
      swiperTpl: new Swiper('.swiper-container', conf)
    });
  };
  handleFormresetFields = conf => {
    this.state.swiperTpl.destroy(true, true);
    this.setState({
      swiperConf: conf,
      swiperTpl: new Swiper('.swiper-container', conf)
    });
  };
  constructor(props) {
    super(props);
    this.state = {
      swiperTpl: null,
      swiperConf: {
        loop: true,
        itemcount: 3,
        autoplay: true,
        observer : true,
        pagination: {
          clickable :true,
          el: '.swiper-pagination'
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    };
  };
  componentDidMount() {
    this.setState({
      swiperTpl: new Swiper('.swiper-container', this.state.swiperConf)
    })
  };
  render() {
    const { swiperConf } = this.state;
    return (
      <Row className="swpierTpl" id="swpierTpl">
        <Col span={ 24 }>
          <Divider orientation="left">
            <Tag color="blue"></Tag>
          </Divider>
        </Col>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Paragraph><i className="fa fa-cog" aria-hidden="true"></i>&nbsp;</Paragraph>
          <Conf conf={swiperConf} callbackSubmit={this.handleSubmit} callbackReset={this.handleFormresetFields} />
        </Col>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Paragraph><i className="fa fa-eye" aria-hidden="true"></i>&nbsp; </Paragraph>
          <div className="swiper-container">
            <div className="swiper-wrapper">
              {this.swiperitem(swiperConf.itemcount)}
            </div>
            <div className="swiper-pagination" style={{display:swiperConf.pagination?'inline':'none'}}></div>
            <div className="swiper-button-prev" style={{display:swiperConf.navigation?'inline':'none'}}></div>
            <div className="swiper-button-next" style={{display:swiperConf.navigation?'inline':'none'}}></div>
          </div>
        </Col>
      </Row>
    );
  }
}

export default Swiperloop;
